body{display:inline-block}:root{box-sizing:border-box;font-size:calc(.5em + 1vw);line-height:1.2;-ms-user-select:none;user-select:none;-webkit-user-select:none;background-color:#ffc048;font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif}*,::before,::after{box-sizing:inherit}.inner-container{display:flex;flex-direction:column;align-items:center;position:absolute;top:50%;left:50%;transform:translateY(-50%) translateX(-50%);margin:auto;width:auto;text-align:center;background-color:#ffb11b;padding:1em}.inner-container .pad-bank{margin:auto;width:90%;height:60%;display:inline-block}.inner-container .pad-bank .drum-pad{position:relative;float:left;width:30%;height:20%;margin-right:.7em;border-radius:.3em;padding:1em;cursor:pointer}.inner-container .controls-container{width:240px;height:92%;display:inline-block;margin:40px 20px 0 10px;vertical-align:top}.inner-container .controls-container .control{width:100px;margin:auto}.inner-container .controls-container .control p{margin-bottom:0}.inner-container .controls-container #display{width:200px;background-color:gray;margin:15px auto;padding:15px;box-sizing:border-box}.outer-select,.inner-container .controls-container .control .select{margin:auto;border:1px solid black;width:50px;height:20px;padding:1px;background-color:black}.inner-select,.inner-container .controls-container .control .select .inner{width:23px;height:19px;background:blue;border:1px solid black;box-sizing:border-box;cursor:pointer}@media screen and (max-width: 650px){.inner-container{transform:scale(0.7)}}
